import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Form, Input, Row, Col } from 'antd';
import BtnSave from 'components/common/button/BtnSave';
import {
  getData,
  configReportTitle
} from 'services/reportTitle';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    tenbo: '',
    tentruong: '',
    chucdanh1: '',
    chucdanh2: '',
    nguoiky1: '',
    nguoiky2: '',
    noiky: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormReportTitleConfig' })
class ReportTitleConfig extends Component {
  state = {
    initialValue: {},
  };

  componentDidMount = () => {
    this.getData();
  };

  getData = async () => {
    const res = await getData();
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  }

  saveData = async values => {
    try {
      await configReportTitle(values);
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thiết lập tiêu đề báo cáo">
        <Row>
          <Col span={24}>
            <Form onSubmit={this.handleSubmit}>
              <Form.Item label="Tiêu đề tên bộ" {...formItemLayout}>
                {getFieldDecorator('tenbo', {
                  initialValue: initialValue.tenbo,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input placeHolder='ĐẠI HỌC SÂN KHẤU ĐIỆN ẢNH' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề tên trường" {...formItemLayout}>
                {getFieldDecorator('tentruong', {
                  initialValue: initialValue.tentruong,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input placeHolder='PHÒNG TÀI VỤ' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề chức danh 1" {...formItemLayout}>
                {getFieldDecorator('chucdanh1', {
                  initialValue: initialValue.chucdanh1,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input placeHolder='NGƯỜI LẬP BIỂU' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề chức danh 2" {...formItemLayout}>
                {getFieldDecorator('chucdanh2', {
                  initialValue: initialValue.chucdanh2,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input placeHolder='TRƯỞNG PHÒNG TÀI VỤ' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề tên người ký 1" {...formItemLayout}>
                {getFieldDecorator('nguoiky1', {
                  initialValue: initialValue.nguoiky1,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input placeHolder='NGUYỄN VĂN A' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề tên người ký 2" {...formItemLayout}>
                {getFieldDecorator('nguoiky2', {
                  initialValue: initialValue.nguoiky2,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input placeHolder='NGUYỄN VĂN B' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề nơi ký" {...formItemLayout}>
                {getFieldDecorator('noiky', {
                  initialValue: initialValue.noiky,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input placeHolder='HCM, ngày 08 tháng 08 năm 2019' />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
              </Form.Item>
            </Form>
          </Col>
        </Row>
      </Card>
    )
  }
}

export default props => (
  <Page {...props}>
    <ReportTitleConfig {...props} />
  </Page>
);


